import React, { Component } from 'react';
import { postAction } from '@/axios';
import moment from 'moment';
import { message, Table, Divider, Modal } from 'antd';
import { withRouter } from 'react-router-dom';
import FilterBox from './filterBox';
import styles from './index.module.less';
import {hasButtonPermission} from '@/util'
import HandleAllModal from './handleAllModal';
import PreviewDoc from '@/pages/common/PreviewDoc';
const statusList = [//
    // {label:"",value:0,color:""},
    {label:"",value:1,color:"rgb(250, 219, 20)"},
    {label:"",value:2,color:"rgb(82, 196, 26)"},
    {label:"",value:3,color:"rgb(250, 84, 28)"},
    {label:"",value:4,color:"yellow"}
]
const auditTypeList = [//
    {label:"",value:1},
    {label:"",value:2},
    {label:"",value:3},
]
class AuditManagement extends Component {
    constructor(props){
        super(props);
        this.state = {
            filterValue:{},
            columns:[
                {
                    title:"",
                    dataIndex:"sortIndex",
                    width:"60px",
                },{
                    title:"",
                    dataIndex:"nodeTaskCode",
                    render: (text) => (<span title={text}>{text}</span>)
                },{
                    title:"",
                    dataIndex:"nodeTaskName",
                    render:(text)=>(<span title={text}>{text}</span>)
                },{
                    title:"",
                    dataIndex:"creatorName",
                    render: (text) => (<span title={text}>{text}</span>)
                },{
                    title:"",
                    dataIndex:"taskStartTime",
                    render:(text)=>{
                        let str = moment.unix(text/1000).format("YYYY-MM-DD HH:mm:ss");
                        if(text){
                            return (<span title={str}>{str}</span>)
                        }else{
                            return "-"
                        }
                    }
                },{
                    title:"",
                    dataIndex:"taskEndTime",
                    render:(text)=>{
                        let str =  moment.unix(text/1000).format("YYYY-MM-DD HH:mm:ss");
                        if(text){
                            return (<span title={str}>{str}</span>)
                        }else{
                            return "-"
                        }
                    }
                },{
                    title:"",
                    dataIndex:"processType",
                    render:(text) => {
                        const type = auditTypeList.find(item => item.value === text);
                        let str = type?type.label:"";
                        return  (<span title={str}>{str}</span>)
                    }
                },{
                    title:"",
                    dataIndex:"appNodeTaskStatus",
                    render:(text,record) => {
                        if(record.isTimeOut===1){
                            return <React.Fragment><i style={{width:8,height:8,display:"inline-block",borderRadius:"50%",marginRight:5,background:"purple"}}></i></React.Fragment>
                        }
                        const status = statusList.find(item => item.value === text);
                        return status?<React.Fragment><i style={{width:8,height:8,display:"inline-block",borderRadius:"50%",marginRight:5,background:status.color}}></i>{status.label}</React.Fragment>:""
                    }
                },{
                    title:"",
                    width:"120px",
                    render:(text,record)=>{
                        if(record.appNodeTaskStatus===3){//
                            return hasButtonPermission('audit003')? 
                            <span className="operation-link" onClick={()=>{this.lookUp(record)}}></span>:null
                        }else if(record.appNodeTaskStatus===2){//
                            if(record.processType===1){//
                                return hasButtonPermission('audit003')? 
                                <span className="operation-link" onClick={()=>{this.lookUp(record)}}></span>:null
                            }else{
                                return hasButtonPermission('audit001') ?
                                <span className="operation-link" onClick={()=>{this.preview(record)}}></span>:null
                            }
                        }else{///
                            if(record.processType===1){//
                                return hasButtonPermission('audit002') ?<span className="operation-link" onClick={()=>{this.audit(record)}}></span>:null
                            }else{
                                return (
                                    <React.Fragment>
                                        {
                                            hasButtonPermission('audit002') && <React.Fragment>
                                                <span className="operation-link" onClick={()=>{this.audit(record)}}></span>
                                                <Divider type="vertical"/>
                                            </React.Fragment>
                                        }
                                        {
                                            hasButtonPermission('audit001')&&
                                            <span className="operation-link" onClick={()=>{this.preview(record)}}></span>
                                        }
                                    </React.Fragment>
                                )
                            }
                        }
                    },
                }
            ],
            dataSource:[
                // {sortIndex:"1",nodeTaskCode:"9927",nodeTaskName:"",userName:"",taskEndTime:"2018-03-18 19:00:00",taskStartTime:"2018-01-11 19:00:00",processType:1,appNodeTaskStatus:2,id:"1"},
            ],
            pagination:{
                current: 1,
                pageSize: 10,
                total:0,
                showTotal: (total) => (`${total}`),
                showQuickJumper:true,
                // showSizeChanger:true,
            },
            businessId:"",//id,
            showItem:{},
            selectedRowKeys:null,
            selectedRows: null,
            handleAllModalStatus: false
        }
    }
    componentDidMount(){
        this.getApproveNodeTaskList({});
    }
    //
    getApproveNodeTaskList(data,page){
        let current = 1;
        if(page==="page"){
            current = this.state.pagination.current;
        }
        let url = `/ybt/wf/approveTask/getApproveNodeTaskList?pageNum=${current}&pageSize=${this.state.pagination.pageSize}`;
        postAction(url,data).then(res => {
            if(res.success){
                //
                let pagination = JSON.parse(JSON.stringify(this.state.pagination));
                pagination.total = res.obj.total;
                pagination.current = res.obj.page;
                pagination.pageSize = res.obj.pageSize;

                //
                let dataSource = res.obj.rows||[];
                dataSource.forEach((item,index)=>{
                     item.sortIndex = index+1;
                });
                // console.log(dataSource);
                this.setState({
                    dataSource,
                    pagination
                })
            }else{
                message.error(res.obj)
            }
        })
    }
    //
    handleSearch = (filterValue, type) => {
        if(type === 'reset'){
            this.setState({
                pagination:{
                    ...this.state.pagination,
                    current: 1,
                }
            }, () => {
                this.setState({filterValue});
                this.getApproveNodeTaskList(filterValue);
            })
            return false;
        }
        // console.log("",filterValue);
        this.setState({filterValue});
        this.getApproveNodeTaskList(filterValue);
    }
    handleTableChange=(pagination)=>{
        this.setState({
            pagination:pagination
        },()=>{
            this.getApproveNodeTaskList(this.state.filterValue,"page");
        })
    }
    //
    preview = (record) => {
        this.setState({
            showPDF:true,
            businessId:record.businessId,//id
            showItem:record
        })
    }
    //
    lookUp = (record) => {
        // message.info("");
        this.props.history.push("/auditManagement/lookUp?id="+record.processNodeTaskId)
    }
    //
    audit = (record) => {
        // console.log(record);
        this.props.history.push("/auditManagement/audit?id="+record.processNodeTaskId)
    }

    // 
    handleAll = () => {
        let selectedRows = this.state.selectedRows;
        if (!selectedRows || !selectedRows[0]) {
            message.warn('')
            return false
        } else {
            let bool = true
            selectedRows.forEach(
                (item) => {
                    if (item.appNodeTaskStatus !== 1) {
                        bool = false;
                        return false
                    }
                }
            )
            if (!bool) {
                message.warn('', 8)
                return false
            } else {
                this.setState({
                    handleAllModalStatus: true
                })
            }
        }
    }

    // 
    hideHandleAllModal = () => {
        this.setState({
            handleAllModalStatus: false
        })
    }

    // 
    reloadTable = () => {
        this.getApproveNodeTaskList(this.state.filterValue,"page")
    }

    //
    clearAllCheckBox = () => {
        this.setState({
            selectedRowKeys: null,
            selectedRows: null
        })
    }
    
    render() {
        const rowSelection = {
            selectedRowKeys:this.state.selectedRowKeys,
            onChange: (selectedRowKeys, selectedRows) => {
              this.setState({
                selectedRowKeys,
                selectedRows
              })
            }
          };

        return (
            <div className={styles.container}>
                <div className={styles.card}>
                    <div className={styles.cardTitle}>
                        
                    </div>

                    <FilterBox 
                        updateFilterValue = {(filterValue, type) => {this.handleSearch(filterValue, type)}} 
                        handleAll = {this.handleAll}
                        clearAllCheckBox = {this.clearAllCheckBox}
                    />

                    <div className={styles.table}>
                        <Table
                            rowKey={recode => recode.nodeTaskCode}
                            columns={this.state.columns}
                            dataSource={this.state.dataSource}
                            pagination={this.state.pagination}
                            onChange={this.handleTableChange}
                            rowSelection = {rowSelection}
                        />
                    </div>

                </div>

                <Modal
                    visible={this.state.showPDF}
                    // title=""
                    title={<span style={{fontWeight:"bold"}}></span>}
                    width={1200}
                    bodyStyle={{padding:0,height:"70vh"}}
                    destroyOnClose={true}
                    footer={false}
                    onCancel={()=>{this.setState({showPDF:false})}}
                >
                    <PreviewDoc id={this.state.showItem.businessId} type={this.state.showItem.processType===2?'formId':'formTaskId'}/>
                </Modal>

                <HandleAllModal 
                    handleAllModalStatus = {this.state.handleAllModalStatus} 
                    hideHandleAllModal = {this.hideHandleAllModal}
                    selectedRows = {this.state.selectedRows}
                    reloadTable = {this.reloadTable}
                />
                
            </div>
        )
    }
}

export default withRouter(AuditManagement);